import {SafeAreaView} from 'react-native-safe-area-context';
import WebView from 'react-native-webview';
import {BASE_URL_TICKET} from '../../../../../services/httpClient';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {String} from '../../../../../constants';
import {useRoute} from '@react-navigation/native';

const ViewFlowDetail = () => {
  const route = useRoute<any>();
  const selectTabBottom = route?.params?.selectTabBottom ?? 1;
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {accessToken} = useAppSelector(state => state.common);
  return (
    <SafeAreaView edges={['bottom']} style={{flex: 1, backgroundColor: '#fff'}}>
      <WebView
        startInLoadingState
        injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
        source={{
          uri:
            BASE_URL_TICKET +
            `${dataTicketRedux.id}?${
              selectTabBottom === 1 ? '_typeView=mine&' : ''
            }access_token=${accessToken}`,
        }}
      />
    </SafeAreaView>
  );
};

export default ViewFlowDetail;
