import {useState} from 'react';
import BackgroundFlow from './components/BackgroundFlow';
import Flow from './components/Flow';
import HistoryTicket from './components/HistoryTicket';
import TicketRelative from './components/TicketRelative';
import styles from './styles';
import Document from './components/Document';
import SLA from './components/SLA';
import TextCM from '../../../../components/Text';
import {FlatList, ScrollView, TouchableOpacity, View} from 'react-native';
import {SafeAreaView, useSafeAreaInsets} from 'react-native-safe-area-context';
import {SizeDP} from '../../../../../constants/Size';
import {ITicket} from '../../../../../model/interface/service.interface';

const TabViewCM = ({
  selectTabBottom,
  dataTicket,
}: {
  selectTabBottom: number;
  dataTicket: ITicket;
}) => {
  const [index, setIndex] = useState<any>({
    key: 'background',
    title: 'Tng quan',
  });

  const [routes] = useState([
    {key: 'background', title: 'Tng quan'},
    // {key: 'flow', title: 'Quy trnh'},
    {key: 'document', title: 'Ti liu nh km'},
    {key: 'hsitoryTicket', title: 'Lch s ticket'},
    {key: 'ticketRelative', title: 'Ticket lin quan'},
    {key: 'sla', title: 'Thng tin SLA'},
  ]);

  const renderItems = (item: any) => {
    switch (item.key) {
      case 'background':
        return <BackgroundFlow selectTabBottom={selectTabBottom} />;
      case 'flow':
        return <Flow />;
      case 'document':
        return <Document dataTicket={dataTicket} />;
      case 'hsitoryTicket':
        return <HistoryTicket />;
      case 'ticketRelative':
        return <TicketRelative />;
      case 'sla':
        return <SLA />;
      default:
        return <View></View>;
    }
  };

  const renderTabView = ({item}: {item: any}) => {
    return (
      <TouchableOpacity
        style={[styles.ctnMainTab, index.key === item.key && styles.activeTab]}
        onPress={() => setIndex(item)}>
        <TextCM
          style={[styles.tabBar, index.key === item.key && styles.colorActive]}>
          {item.title}
        </TextCM>
      </TouchableOpacity>
    );
  };
  const inset = useSafeAreaInsets();

  return (
    <>
      <FlatList
        horizontal
        data={routes}
        keyExtractor={(item, index) => String(item.key)}
        showsHorizontalScrollIndicator={false}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={{height: SizeDP(50)}}
        renderItem={renderTabView}
      />
      <View style={{height: SizeDP(6), backgroundColor: '#F8FAFE'}} />
      {renderItems(index)}
    </>
  );
};

export default TabViewCM;
