const getSize = (size: number) => {
  if (size && size >= 1024) {
    return Math.floor(size / 1024) + ' KB';
  } else if (size && size < 1024) {
    return size + ' MB';
  } else {
    return '-';
  }
};

export default {
  getSize,
};
