//import liraries
import React, {useRef, useState} from 'react';
import {View, TouchableOpacity, ScrollView, FlatList} from 'react-native';
import TextCM from '../../../../../../components/Text';
import {
  IconArrowHeader,
  IconOpenSize,
} from '../../../../../../../assets/images';
import {ScreenHeight, SizeDP} from '../../../../../../../constants/Size';
import styles from './styles';
import WebView, {WebViewMessageEvent} from 'react-native-webview';
import {useAppSelector} from '../../../../../../../hooks/useRedux';
import {get, isNull, isUndefined} from 'lodash';
import {SafeAreaView} from 'react-native-safe-area-context';
import {useNavigation} from '@react-navigation/native';
import {Color, NavigationName, String} from '../../../../../../../constants';
import {
  BASE_URL_FORM,
  BASE_URL_TICKET,
} from '../../../../../../../services/httpClient';
// create a component

const BackgroundFlow = ({selectTabBottom}: {selectTabBottom: number}) => {
  const webviewRef = useRef<WebView>(null);
  const navigation = useNavigation<any>();
  const [isShowForm, setIsShowForm] = useState<boolean>(true);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {accessToken} = useAppSelector(state => state.common);

  const handleGetInfoForm = () => {
    const formDataInTicket = get(dataTicketRedux, 'formData[0].formData', null);
    const formData = JSON.stringify({
      type: 'postFormEdit',
      data: formDataInTicket ? JSON.parse(formDataInTicket) : null,
      isReadonly: true,
    });
    const scriptJSX = `window.postMessage('${formData}', '*');`;
    webviewRef?.current?.injectJavaScript(scriptJSX);
  };

  const onMessage = (message: WebViewMessageEvent) => {
    const data = JSON.parse(message.nativeEvent.data);
    if (data?.result === 'e-from') {
      handleGetInfoForm();
    }
  };

  const handleMessageFlow = (message: WebViewMessageEvent) => {
    const data = JSON.parse(message.nativeEvent.data);
    console.log('data', data);
  };

  return (
    <SafeAreaView
      style={{height: ScreenHeight - SizeDP(264)}}
      edges={['bottom']}>
      <ScrollView style={styles.ctnMain}>
        <View
          style={{
            height: (ScreenHeight - SizeDP(264)) / 2,
            width: '100%',
            position: 'relative',
          }}>
          <WebView
            containerStyle={{width: '100%'}}
            startInLoadingState
            injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
            source={{
              uri:
                BASE_URL_TICKET +
                `${dataTicketRedux.id}?${
                  selectTabBottom === 1 ? '_typeView=mine&' : ''
                }access_token=${accessToken}`,
            }}
            onMessage={handleMessageFlow}
          />
          <TouchableOpacity
            style={styles.ctnMainOpen}
            onPress={() =>
              navigation.navigate(NavigationName.ViewFlowDetail, {
                selectTabBottom: selectTabBottom,
              })
            }>
            <View style={styles.openIcon}>
              <IconOpenSize width={SizeDP(16)} height={SizeDP(16)} />
            </View>
          </TouchableOpacity>
        </View>
        <View style={styles.spreadView}></View>
        <View style={styles.rowCenterBetwen}>
          <TextCM style={styles.ticketInfo}>Thng tin ticket</TextCM>
          <TouchableOpacity
            onPress={() => setIsShowForm(!isShowForm)}
            hitSlop={SizeDP(15)}
            style={{
              transform: [{rotate: isShowForm ? '-90deg' : '90deg'}],
            }}>
            <IconArrowHeader width={SizeDP(16)} height={SizeDP(16)} />
          </TouchableOpacity>
        </View>
        {isShowForm && !isUndefined(dataTicketRedux) && (
          <View
            style={{height: (ScreenHeight - SizeDP(264)) / 2, width: '100%'}}>
            <WebView
              ref={webviewRef}
              startInLoadingState
              onMessage={onMessage}
              injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
              allowsInlineMediaPlayback={true}
              mediaPlaybackRequiresUserAction={false}
              containerStyle={{
                height: (ScreenHeight - SizeDP(264)) / 2,
                width: '100%',
              }}
              source={{
                uri:
                  BASE_URL_FORM +
                  dataTicketRedux?.formData?.filter(val =>
                    isNull(val.userTaskId),
                  )[0]?.version,
              }}
            />
          </View>
        )}
      </ScrollView>
    </SafeAreaView>
  );
};

//make this component available to the app
export default BackgroundFlow;
