import {BASE_URL} from './httpClient';

export default class ReviewTicketServices {
  static async getInfoFile(body: any, accessToken: string) {
    const endPoint = `/services/erequest/api/file-repositories/getInformationFile`;
    try {
      const data = await fetch(BASE_URL + endPoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
        body: JSON.stringify(body),
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getListFormModelerById(id: string, accessToken: string) {
    try {
      const endPoint = `/services/idaas-eflow/api/forms/modeler/` + id;
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getFormDetailById(id: string, accessToken: string) {
    try {
      const endPoint = `/services/eform/api/forms/` + id;
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getStatusParty(ticketId: string, accessToken: string) {
    try {
      const endPoint =
        `/services/erequest/api/ticket-recipients/statusRaw/` + ticketId;
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async getListFileRelative(params: any, accessToken: string) {
    try {
      const endPoint =
        `/services/erequest/api/attachments/get-by-ticketId?` +
        new URLSearchParams(params).toString();
      const data = await fetch(BASE_URL + endPoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer' + accessToken,
        },
      });
      const totalData = data.headers.get('x-total-count') ?? 0;
      const listData = await data.json();
      return Promise.resolve({
        data: listData,
        totalRecords: Number(totalData),
      });
    } catch (error) {
      Promise.reject(error);
    }
  }

  static async viewFile(body: FormData, accessToken: string) {
    try {
      const endPoint = `/services/s3service/api/view`;
      const data = await fetch(BASE_URL + endPoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer' + accessToken,
        },
        body: body,
      });
      return Promise.resolve(data.json());
    } catch (error) {
      Promise.reject(error);
    }
  }
}
