import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderTicketDetail from '../components/Header';
import TextCM from '../../../components/Text';
import {useCallback, useEffect, useState} from 'react';
import {IComments} from './interface';
import {ListServices} from '../../../../services';
import {useFocusEffect, useRoute} from '@react-navigation/native';
import {ITicket} from '../../../../model/interface/service.interface';

const CommunicationTicketScreen = () => {
  const route = useRoute();
  const dataTicket = (route?.params as any)?.dataTicket as ITicket;
  const [listComments, setListComments] = useState<IComments[]>([]);

  useFocusEffect(
    useCallback(() => {
      getListComments();
    }, []),
  );

  const getListComments = async () => {
    try {
      const params = {
        userTaskId: 'all',
        ticketId: dataTicket?.id,
        sort: 'lastModifiedDate,desc',
      };
      const data = await ListServices.getListComments(params);
      console.log(data.content);
    } catch (error) {}
  };

  return (
    <>
      <HeaderTicketDetail />
      <SafeAreaView
        edges={['bottom']}
        style={{flex: 1, alignItems: 'center', justifyContent: 'center'}}>
        <TextCM>Tnh nng ang pht trin</TextCM>
      </SafeAreaView>
    </>
  );
};

export default CommunicationTicketScreen;
